import { useMemo } from 'react';
import { TFunction, useTranslation } from 'react-i18next';
import {
  CashbackType,
  DealSummary,
  DealSummaryWithDomains,
} from '../../api/types';
import { DealOpenMessage } from '../../common/messageTypes';
import { formatMoney } from '../../common/money';
import env from '../../env';

const getFixedCashbackText = (deal: DealSummary, t: TFunction) => {
  if (deal.cashbackType === CashbackType.fixed) {
    return t('deals.cashback', {
      cashback: formatMoney(deal.currency ?? 'GBP', deal.cashback),
    });
  }

  return null;
};

export const formatCashback = (cashback: number) => {
  let formattedNumber = +cashback.toFixed(0);

  if (cashback < 10) {
    formattedNumber = +cashback.toFixed(2);
  } else if (cashback < 100) {
    formattedNumber = +cashback.toFixed(1);
  }

  return `${formattedNumber}%`;
};

export const useCashbackText = (deal: DealSummary | null, lowered = false) => {
  const { t } = useTranslation();

  return useMemo(() => {
    if (!deal) {
      return '';
    }

    if (env.REACT_APP_IS_O2 || deal.cashback <= 0) {
      return lowered ? t('deals.deal-lowered') : t('deals.deal');
    }

    if (deal.cashbackType === CashbackType.fixed) {
      const fixedCashbackText = getFixedCashbackText(deal, t);

      if (fixedCashbackText) {
        return fixedCashbackText;
      }
    }

    return lowered
      ? t('deals.cashback-lowered', { cashback: formatCashback(deal.cashback) })
      : t('deals.cashback', { cashback: formatCashback(deal.cashback) });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [deal, lowered]);
};

export const backgroundOpenDeal = (
  deal: DealSummaryWithDomains,
  sameTab = true,
  isAnonymous?: boolean | null
) => {
  if (isAnonymous) {
    return;
  }

  return new Promise<boolean>((resolve) => {
    const message: DealOpenMessage = {
      type: 'openDeal',
      deal,
      sameTab,
    };

    browser.runtime.sendMessage(message).then(resolve);
  });
};
