import React from 'react';
import Switch from 'react-switch';
import styled, { useTheme } from 'styled-components';

const Label = styled.label`
  display: flex;
  align-items: center;
  cursor: pointer;
`;

const LabelText = styled.span`
  flex: 1;
  margin-right: 10px;
  font-size: 14px;
`;

const StyledSwitch = styled(Switch)`
  vertical-align: bottom;
`;

interface ToggleProps {
  label: string;
  checked: boolean;
  onChange: (checked: boolean) => void;
  width?: number;
  height?: number;
  loading?: boolean;
  className?: string;
}

/*
This component is a wrapper for https://github.com/markusenglund/react-switch#readme 
*/
const Toggle: React.FC<ToggleProps> = ({
  label,
  checked,
  onChange,
  width = 29,
  height = 15,
  loading = false,
  className,
}) => {
  const { colors } = useTheme();

  return (
    <Label className={className}>
      <LabelText>{label}</LabelText>
      {!loading && (
        <StyledSwitch
          checked={checked}
          onChange={onChange}
          width={width}
          height={height}
          onColor={colors.secondaryLight}
          offColor={colors.neutralLight}
          uncheckedIcon={false}
          checkedIcon={false}
        />
      )}
    </Label>
  );
};

export default Toggle;
