import React, { useState, useEffect, useRef } from 'react';
import styled from 'styled-components';
import { useTranslation } from 'react-i18next';
import clsx from 'clsx';
import { Card } from '../common/components';
import { themeColor } from '../common/theming';
import { CouponCode } from '../api/types';
import { writeToClipboard } from '../common/clipboard';

const StyledCard = styled(Card)`
  height: 70px;
  padding: 10px;
  display: grid;
  grid-template-columns: 1fr auto;
  grid-template-rows: auto auto;
  transition: border-color 0.25s ease-out;
  &.copied {
    border: 1px solid ${themeColor('success')};
  }
`;

const DiscountCode = styled.p`
  grid-area: 1 / 1 / 2 / 2;
  color: ${themeColor('neutralDark')};
  font-size: 14px;
  font-weight: bold;
  margin: 0;
  justify-self: flex-start;
  align-self: center;
  transition: color 0.25s ease-out;
  .copied & {
    color: ${themeColor('success')};
  }
`;

const Summary = styled.p`
  grid-area: 2 / 1 / 3 / 2;
  margin: 0;
  margin-right: 5px;
  color: ${themeColor('neutral')};
  font-size: 12px;
  justify-self: flex-start;
  align-self: center;
  height: 28px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
`;

const StyledButton = styled.button`
  appearance: none;
  border: none;
  background: none;
  cursor: pointer;
  font: inherit;
  &:disabled {
    opacity: 0.3;
  }
  grid-area: 1 / 2 / 3 / 3;
  font-size: 12px;
  text-decoration: none;
  padding: 10px 20px;
  font-weight: bold;
  color: ${themeColor('neutralDark')};
  border: 1px solid ${themeColor('neutralLight')};
  justify-self: center;
  align-self: center;
  border-radius: 2px;
  transition: color 0.25s ease-out, border-color 0.25s ease-out;
  .copied & {
    color: ${themeColor('success')};
    border: 1px solid ${themeColor('success')};
  }
`;

const ForceRedrawComponent = styled.span`
  grid-area: 1 / 1 / 2 / 2;
  justify-self: flex-end;
  align-self: flex-end;
  &::selection {
    color: ${themeColor('neutralDark')};
    background-color: ${themeColor('neutralLighter')};
    text-shadow: none;
  }
`;

interface CopyButtonProps {
  copied: boolean;
  onClick(): void;
}

const CopyButton: React.FC<CopyButtonProps> = ({ onClick, copied }) => {
  const { t } = useTranslation();
  const redrawRef = useRef<HTMLSpanElement>(null);

  useEffect(() => {
    // This is to force a rerender as sometimes chrome decides not to
    // It only appears to be for some people and this fixes it
    if (redrawRef.current) {
      const selection = window.getSelection();
      const range = document.createRange();
      range.selectNodeContents(redrawRef.current);
      selection?.removeAllRanges();
      selection?.addRange(range);
      setTimeout(() => {
        selection?.removeAllRanges();
      }, 100);
    }
  });

  return (
    <>
      <ForceRedrawComponent ref={redrawRef}>&nbsp;</ForceRedrawComponent>
      <StyledButton onClick={onClick}>
        {copied ? t('coupon.copied') : t('coupon.copy')}
      </StyledButton>
    </>
  );
};

interface CouponCardProps {
  coupon: CouponCode;
}

const CouponCard: React.FC<CouponCardProps> = ({ coupon }) => {
  const [copied, setCopied] = useState(false);

  const handleCopyClick = () => {
    writeToClipboard(coupon.code ?? '');
    setCopied(true);
  };

  return (
    <StyledCard className={clsx({ copied })}>
      <DiscountCode>{coupon.code}</DiscountCode>
      <Summary title={coupon.summary}>{coupon.summary}</Summary>
      <CopyButton onClick={handleCopyClick} copied={copied} />
    </StyledCard>
  );
};

export default CouponCard;
