import i18next from 'i18next';
import React, { useEffect, useState } from 'react';
import { I18nextProvider } from 'react-i18next';
import { ThemeProvider } from './common/theming';
import env from './env';
import Main from './popup/Main';

const App: React.FC = () => {
  const [hasGivenAccess, setHasGivenAccess] = useState(false);

  useEffect(() => {
    const run = async () => {
      const websiteUrl = new URL(env.WEBSITE_URL);
      const websiteHostname = websiteUrl?.hostname;

      const givenPermission = await browser.permissions.contains({
        origins: [`*://*.${websiteHostname}/*`],
      });

      setHasGivenAccess(givenPermission);
    };

    run();
  }, []);
  return (
    <ThemeProvider>
      <I18nextProvider i18n={i18next}>
        {/* {hasGivenAccess ? 'Given permission!' : 'You need to give us permissions'} */}
        <Main />
      </I18nextProvider>
    </ThemeProvider>
  );
};

export default App;
