import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import clsx from 'clsx';
import { useTranslation } from 'react-i18next';
import { themeColor } from '../common/theming';
import { DealSummaryWithDomains } from '../api/types';
import { hasActivatedDeal } from '../api/services/helpers';
import CDNImage from '../contentapp/components/CDNImage';
import { Card } from '../common/components';
import { backgroundOpenDeal, useCashbackText } from './common/deal';
import { setInteraction } from '../api/services/deals';

const Button = styled.button`
  background: ${themeColor('primary')};
  color: ${themeColor('neutralDark')};
  border: none;
  outline: none;
  font-weight: bold;
  border-radius: 2px;
  font-size: 14px;
  white-space: nowrap;
  padding: 10px 30px;
  &:disabled {
    opacity: 0.5;
  }
`;

const StyledCard = styled(Card)`
  padding: 10px;
  display: grid;
  grid-template-columns: auto 1fr;
  justify-items: center;
  align-items: center;
  height: 70px;
  & img {
    height: 50px;
    width: 60px;
    margin: 0 10px 0 0;
    object-fit: contain;
  }
  & button {
    margin: 0 6px;
    height: 37px;
    font-size: 14px;
    background: ${themeColor('neutralDark')};
    color: ${themeColor('neutralLighter')};
    border-radius: 2px;
    border: none;
    padding: 0;
    justify-self: stretch;
    :disabled {
      opacity: 1;
      cursor: default;
    }
  }
  &.activated {
    border: 1px solid ${themeColor('success')};
    & button {
      border: 1px solid ${themeColor('success')};
      background: ${themeColor('neutralLighter')};
      color: ${themeColor('success')};
    }
  }
`;

interface MainDealCardProps {
  deal: DealSummaryWithDomains;
  domain: string;
}

const MainDealCard: React.FC<MainDealCardProps> = ({ deal, domain }) => {
  const { t } = useTranslation();

  const [showActivated, setShowActivated] = useState(false);
  const cashbackText = useCashbackText(deal, !showActivated);

  useEffect(() => {
    async function run() {
      setShowActivated(await hasActivatedDeal(domain));
    }

    run();
  }, [domain]);

  const handleOpenDeal = async () => {
    await backgroundOpenDeal(deal);
    await setInteraction(domain, { type: 'popup' });
    window.close();
  };

  return (
    <StyledCard className={clsx({ activated: showActivated })}>
      <CDNImage src={deal?.logo} dimensions={{ width: 60, height: 50 }} />
      <Button onClick={handleOpenDeal} disabled={showActivated}>
        {showActivated ? (
          <>
            {cashbackText} {t('deals.now-active')}
          </>
        ) : (
          <>
            {t('deals.activate')} {cashbackText}
          </>
        )}
      </Button>
    </StyledCard>
  );
};

export default MainDealCard;
