import React from 'react';
import { stringifyUrl } from 'query-string';
import env from '../../env';

type Dimension = string | number | null;

export const getCDNUrl = (
  src: string | null = null,
  width: Dimension = null,
  height: Dimension = null
) => {
  if (src === null) {
    return '';
  }

  const url =
    src?.startsWith('http://') || src?.startsWith('https://')
      ? src
      : `${env.CDN_URL}/${src}`;

  if (width === null || height === null) {
    return url;
  }

  return stringifyUrl({
    url,
    query: { width: width.toString(), height: height.toString() },
  });
};

interface ImageDimensions {
  width: number;
  height: number;
}

export interface CDNImageProps
  extends React.ImgHTMLAttributes<HTMLImageElement> {
  dimensions?: ImageDimensions;
}

const CDNImage: React.FC<CDNImageProps> = ({
  src,
  dimensions = {},
  className,
  alt,
  ...rest
}) => {
  const cdnSource = getCDNUrl(src, dimensions.width, dimensions.height);

  return (
    <img {...rest} alt={alt ?? ''} className={className} src={cdnSource} />
  );
};

export default CDNImage;
