import React, { useEffect, useState } from 'react';
import { lookupByDomain } from '../api/services/deals';
import { DealSummaryWithDomains } from '../api/types';
import { LoadingIndicator } from '../common/components';
import { getCurrentTab, getDomainFromUrl } from '../common/helpers';
import DealsFound from './DealsFound';
import NoDealsFound from './NoDealsFound';

const CurrentTabContent: React.FC = () => {
  const [deal, setDeal] = useState<DealSummaryWithDomains | null>(null);
  const [loading, setLoading] = useState(true);
  const [domain, setDomain] = useState<string | null>(null);

  useEffect(() => {
    const run = async () => {
      const tab = await getCurrentTab();
      if (tab.url === undefined || tab.id === undefined) {
        setDomain(null);
        setDeal(null);
        setLoading(false);
        return;
      }
      const domain = getDomainFromUrl(tab.url);

      if (domain) {
        const deal = await lookupByDomain(domain);

        setDomain(domain);
        setDeal(deal);
      }
      setLoading(false);
    };

    run();
  }, []);

  if (loading) return <></>;

  if (!deal) return <NoDealsFound domain={domain ?? ''} />;

  if (deal && domain) return <DealsFound deal={deal} domain={domain} />;

  return <div>{deal?.storeName}</div>;
};

export default CurrentTabContent;
