import { getInteractionByDomain } from './deals';

export const hasActivatedDeal = async (domain: string) => {
  const interactionByDomain = await getInteractionByDomain(domain);
  return (
    Boolean(interactionByDomain) &&
    interactionByDomain?.type !== 'dismiss' &&
    interactionByDomain?.type !== 'error'
  );
};
