import React from 'react';
import { useTranslation } from 'react-i18next';
import styled from 'styled-components';
import { DealSummaryWithDomains } from '../api/types';
import { useCoupons } from '../common/deal';
import SectionHeader from './components/SectionHeader';
import CouponCard from './CouponCard';
import MainDealCard from './MainDealCard';

const HeaderContainer = styled.div`
  position: relative;
  width: 100%;
`;

interface Props {
  deal: DealSummaryWithDomains;
  domain: string;
}

const DealsFound: React.FC<Props> = ({ deal, domain }) => {
  const { t } = useTranslation();
  const { codes } = useCoupons(deal);

  return (
    <>
      <HeaderContainer>
        <SectionHeader title={t('store-deals.title')}></SectionHeader>
      </HeaderContainer>
      <MainDealCard deal={deal} domain={domain} />
      {codes.map((c) => (
        <CouponCard key={c.code} coupon={c} />
      ))}
    </>
  );
};

export default DealsFound;
