import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import styled from 'styled-components';
import { Card } from '../../common/components';
import {
  hasAutomaticActivation,
  setAutomaticActivation,
} from '../../common/preferences';
import { themeColor } from '../../common/theming';
import env from '../../env';
import Toggle from '../components/Toggle';

const StyledCard = styled(Card)`
  padding: 10px;
`;

const Explainer = styled.p`
  font-size: 12px;
  line-height: 18px;
  margin: 10px;
  color: ${themeColor('neutral')};
  &::before {
    content: 'i';
    display: inline-block;
    margin-right: 5px;
    width: 14px;
    height: 14px;
    line-height: 14px;
    font-size: 11px;
    text-align: center;
    border: 1px solid ${themeColor('neutral')};
    color: ${themeColor('neutral')};
    border-radius: 7px;
    box-sizing: border-box;
  }
`;

const AutomaticDealActivation: React.FC = () => {
  const { t } = useTranslation();
  const [loading, setLoading] = useState(true);
  const [automaticDealsEnabled, setAutomaticalDealsEnabled] = useState(false);

  useEffect(() => {
    hasAutomaticActivation().then((isActive) => {
      setAutomaticalDealsEnabled(isActive);
      setLoading(false);
    });
  }, []);

  const handleAutomaticDealsChange = async (checked: boolean) => {
    setAutomaticalDealsEnabled(checked);
    await setAutomaticActivation(checked);
  };

  return (
    <>
      <StyledCard>
        <Toggle
          label={
            env.REACT_APP_IS_O2
              ? t('settings.o2.automatic-deals')
              : t('settings.automatic-deals')
          }
          checked={automaticDealsEnabled}
          onChange={handleAutomaticDealsChange}
          loading={loading}
        />
      </StyledCard>
      <Explainer>
        {env.REACT_APP_IS_O2
          ? t('settings.o2.automatic-deals-explainer')
          : t('settings.automatic-deals-explainer')}
      </Explainer>
    </>
  );
};

export default AutomaticDealActivation;
